html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  /* 1 */
  vertical-align: baseline;
  /* 2 */ }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: .67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  /* 1 */
  font: inherit;
  /* 2 */
  margin: 0;
  /* 3 */ }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */ }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */ }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: .35em .625em .75em; }

legend {
  border: 0;
  /* 1 */
  padding: 0;
  /* 2 */ }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

.clearfix::after {
  content: "";
  display: table;
  clear: both; }

html {
  box-sizing: border-box; }

*, *::before, *::after {
  box-sizing: inherit; }

html {
  font-size: 62.5%; }

body {
  font-size: 1.3rem;
  line-height: 1.428;
  font-family: "Varela Round";
  sans-serif, font-weight: 400; }

h1 {
  font-size: 3.157em; }

h2 {
  font-size: 2.369em; }

h3 {
  font-size: 1.777em; }

h4 {
  font-size: 1.333em; }

h1, h2, h3, h4, h5, h6 {
  font-family: inherit; }

h1, h2, h3, h4, h5, h6 {
  color: inherit;
  line-height: 1.1;
  letter-spacing: -.04em;
  font-family: "Poppins";
  sans-serif, font-weight: 600; }

h1, h2, h3 {
  margin-top: 10px;
  margin-bottom: 20px; }

p {
  font-size: 1em;
  line-height: 1.428; }

ol, ul, dl {
  margin-top: 0;
  margin-bottom: 10px; }
  ol:last-child, ul:last-child, dl:last-child {
    margin-bottom: 0; }

@lost flexbox flex;
body {
  min-height: 100vh;
  background: #2726EA; }

img {
  max-width: 100%; }

/*===========================================
=            Structural Partials            =
===========================================*/
header.top-nav {
  color: #212121;
  padding: 2.5rem 4rem;
  width: 300px;
  min-height: 100vh;
  position: fixed;
  text-align: center;
  background: #FAFAFD; }
  header.top-nav::after {
    content: "";
    height: 100%;
    position: absolute;
    right: 0;
    top: 0;
    border-right: 2px solid rgba(255, 255, 255, 0.3); }
  header.top-nav h1 {
    font-size: 2.5em;
    line-height: 1;
    display: inline-block;
    text-transform: uppercase;
    width: 100%;
    letter-spacing: .4em;
    padding: 1.4rem 0 1rem 1.5rem;
    position: relative;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    border: .3em solid #1DE9B6;
    box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.2), 0 0 10px rgba(0, 0, 0, 0.2); }

form.searchbar-form {
  margin-top: 5rem;
  display: flex;
  flex-flow: row wrap; }

.field-row {
  position: relative;
  padding-top: 17px;
  width: 100%;
  margin-bottom: 3rem; }
  .field-row label {
    position: absolute;
    font-size: 1.2rem;
    top: 0;
    left: 0;
    color: #212; }

.searchbar_input {
  appearance: none;
  background: none;
  border: 0;
  border-bottom: 1px solid #E8EAF6;
  font-size: 1.5rem;
  padding: .5rem .4rem .5rem 0;
  width: 100%; }
  .searchbar_input:focus {
    outline: 0;
    border-bottom-color: #5BFFDA; }

.searchbar_select {
  border-radius: 0;
  min-width: 150px; }

input.searchbar_submit {
  background: #304FFE;
  color: white;
  height: 3.4rem;
  border-radius: 0;
  border: 0;
  text-transform: uppercase;
  font-size: 1.4rem;
  width: 100%; }

section.results {
  width: calc(100% - 300px);
  margin-left: 300px;
  lost-flex-container: row;
  padding: 2.5rem;
  border-radius: 2px; }
  section.results article.results-card_rental {
    lost-waffle: 1/4;
    position: relative;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.3);
    background: white; }

header.results-features {
  height: 300px;
  overflow: hidden; }
  header.results-features img {
    width: 100%; }

ul.rental-images {
  margin: 0;
  padding: 0;
  list-style: none; }
  ul.rental-images li:not(:first-child) {
    display: none; }
